.movie-LoadMovies {
  background: #16d47b;
  width: 100%;
  min-height: 50px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

.movie-LoadMovies p {
  font-family: 'Abel', sans-serif;
  font-size:42px;
  padding: 20px;
}



