.movie-grid h1 {
  font-family: 'Abel', sans-serif;
  font-size:42px;
}

.movie-grid-content {
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.movie-grid-element {
  margin: 0 40px 40px 0;
  background: #353535;
  max-height: 430px;
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s;
  overflow: hidden;
}



